.myModal{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0, 0.5);
}

.myModalContent {
    display: flex;
    padding: 25px;
    background:#ecedf6;
    border-radius: 16px;
    min-width: 250px;
    margin: 0px 5px;
}

.myModal.active{
    display: flex;
    justify-content: center;
    align-items: center;
}